body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
*{
  margin: 0;
  padding: 0;
}
.header{
padding: 20px 100px;
background-color: black;
color: white;
display: flex;
justify-content: space-between;
}
.cart-icon .counter{
  right:7px;
  top:-6px;
  position: relative;
  height: 18px;
  width: 18px;
  background-color: blue;
  border-radius: 50%;
  
}
.cart-icon{
  display: flex;
}
.icon{
  
  font-size: 25px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
img{
  height:150px;
}
.cart-wrapper{
  display:inline-flex;
  margin:40px;
  width: 320px;
  box-shadow: 0px 0px 7px black;
  border-radius: 5px;
}
.image-wrapper{
width:180px;
display: flex;
align-items: center;
justify-content: center;
padding:10px;
border-right:1px solid grey;
}
.content{
  flex-grow: 1;
  display:flex;
  flex-direction: column;
  gap:10px;
  padding: 10px;
}
.price{
  margin-bottom: auto;
}
.content button{
  background-color: black;
color:white;
border-radius: 3px;
}
.quantity{
  display: flex;
  align-self: flex-end;
  border-radius: 3px;
  border:1px solid grey;
}
.quantity div{
  height: 25px;
  width: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.quantity > div:first-child, 
.quantity > div:first-child{
background-color: rgba(3, 3, 255, 0.914);
color: white;
}
.quantity > button{
  color: white;
  border-radius: 0;
}
.quantity > button:disabled{
opacity: 0.7;
}
main{
  padding: 20px;
  display: flex;
  flex-wrap: flex;
  min-height: 80vh;
  justify-content: center;
  align-items: center;
  align-content: center;
  gap:50px;
}
